.masonry__container {
	column-count: 4;
	column-gap: 1rem;
	.masonry__item {
		margin-bottom: 1rem;
		border-radius: var(--bs-border-radius);
	}
}

@media screen and (max-width: 768px) {
	.masonry__container {
		column-count: 3;
	}
}

@media screen and (max-width: 576px) {
	.masonry__container {
		column-count: 2;
	}
}