.banner-section-wrapper {
    position: relative;
    .banner-content {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        z-index: 2;
        .banner-content-wrapper {
            border-radius: var(--bs-border-radius-lg);
            position: relative;
            z-index: 1;
        }
        .banner-content-search-box {
            .form-control {
                background: transparent;
                border: none;
            }
        }
    }
    .main-banner {
        .slick-banner{
            &:not(.slick-initialized) {
                display: flex;

                .slick-item {
                    &:first-child {
                        flex-grow: 1;
                        width: 100%;
                        display: block;
                    }

                    display: none;
                }
            }
        }
        .slick-item {
            height: 76vh;
            position: relative;
            &::before{
                content: "";
                position: absolute;
                width: 100%;
                height: 100%;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                background: linear-gradient(270deg, rgba(255, 255, 255, 0.02) 46.56%, rgba(255, 255, 255, 0.7) 74.14%, #FFFFFF 100%);
                z-index: 2;
            }
            .main-banner-image {
                height: 100%;
                width: 100%;
                object-fit: cover;
            }
        }
        .slick-dots {
            position: absolute;
            bottom: 3.125rem;
        }
    }

}

@include media-breakpoint-down(xxl) {
    .banner-content-wrapper {
        .banner-content-title {
            font-size: 2.125rem;
        }
    }
}

@include media-breakpoint-down(md) {
    .banner-section-wrapper {
        .main-banner {
            .slick-item {
                height: 60vh;
            }
            .slick-dots {
                bottom: 1.875rem;
            }
        }
        .banner-content {
            padding: 0;
            .banner-content-title {
                font-size: 1.5rem;
            }
        }
    }
}

// 
.wrapper {
  display: inline-block;
  position: absolute;
  top: 57%;
  left: 20%;
  transform: translate(-50%, -50%);
}
.video-main {
  position: relative;
  display: inline-block;
}
.icon-size{
  font-size: 12px;
}
@mixin video-icon {
  text-align: center;
  border-radius: 50%;
  display: inline-block;
  z-index: 999;
}
.video {
  @include video-icon;
  line-height: 28px;
  color: var(--bs-white);
  background-color: var(--bs-secondary);
}
.video-1 {
  @include video-icon;
  line-height: 28px;
  color: var(--bs-white);
  background-color: var(--bs-primary);
}
.video-2 {
  @include video-icon;
  line-height: 50px;
  background-color: var(--bs-white);
}
.video-3 {
  @include video-icon;
  line-height: 40px;
  background-color: var(--bs-white);
}
@keyframes waves {
  0% {
    -webkit-transform: scale(0.2, 0.2);
    transform: scale(0.2, 0.2);
    opacity: 0;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  }
  50% {
    opacity: 0.9;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=90)";
  }
  100% {
    -webkit-transform: scale(0.9, 0.9);
    transform: scale(0.9, 0.9);
    opacity: 0;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  }
}
.fa-play:before {
  content: "\f04b";
}
@mixin wave-main {
  position: absolute;
  opacity: 0;
  border-radius: 50%;
  z-index: -1;
  animation: waves 3s ease-in-out infinite;
  transform: translateX(-50%) translateY(-50%);
}
.waves {
  @include wave-main;
  background-color: rgb(13 30 103 / 50%);
  right: -25px;
  bottom: -25px;
}
.waves-1 {
  @include wave-main;
  background-color: rgb(40 90 212 / 50%);
  right: -25px;
  bottom: -25px;
}
.waves-2 {
  @include wave-main;
  background: rgb(255 255 255 / 50%);
  right: -50px;
  bottom: -50px;
}
.waves-3 {
  @include wave-main;
  background: rgb(255 255 255 / 50%);
  right: -35px;
  bottom: -35px;
}
.wave-1 {
  -webkit-animation-delay: 0s;
  animation-delay: 0s;
}
.wave-2 {
  -webkit-animation-delay: 1s;
  animation-delay: 1s;
}
.wave-3 {
  -webkit-animation-delay: 2s;
  animation-delay: 2s;
}
.text-compute{
  color:var(--bs-gray-700);
  font-weight: 700;
  &:hover{
    color: var(--bs-primary);
  }
}
.call-svg{
  svg path{
    stroke: var(--bs-white);
  }
}
.video-text {
  margin-left: 90px;
  z-index: 3;
  cursor: pointer;
  font-family: var(--bs-font-family-sans-serif);
}
// banner qloud security
.qloud-security-banner {
  padding: 200px 0 317px 0;
  .a-link {
    text-decoration: underline;
    font-weight: 600;
    color: var(--bs-blue);
  }
}
// banner  qloud security
.banner-box-section {
  border-radius: 10px;
  margin-top: -50px;
  padding: 45px 45px 45px 45px;
  .box-body {
    margin-left: 1.5rem;
  }
  .box-inner {
    position: relative;
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      border-right: $border-width $border-style $gray-800;
    }
    &:last-child {
      &::after {
        content: none;
      }
    }
  }
}

@include media-breakpoint-down(sm) {
    .banner-section-wrapper {
        .main-banner {
            .slick-item {
                height: 40vh;
            }
        }
    }
}